<template>
    <div class="container">
        <!-- 面包屑 -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/admin' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>菜单管理</el-breadcrumb-item>
        </el-breadcrumb>

        <!-- 菜单信息 -->

        <el-card class="box-card">

            <el-row>
                <el-col :span="4">
                    <el-button type="primary" @click="addButton">添加菜单</el-button>
                </el-col>
            </el-row>

            <el-table border stripe :data="tableData" height="500px">
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="foodName" label="菜名"></el-table-column>
                <el-table-column prop="foodPrice" label="价格"></el-table-column>
                <el-table-column prop="foodSales" label="销量"></el-table-column>
                <el-table-column prop="foodWeight" label="重量"></el-table-column>
                <el-table-column prop="" label="操作">
                    <template slot-scope="scope">
                        <el-button type="warning" icon="el-icon-edit" size="small"
                            @click="foodEdit(scope.row)"></el-button>
                        <el-button type="danger" icon="el-icon-delete-solid" size="small"
                            @click="foodRemove(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>


            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="queryInfo.page" :page-sizes="[5, 10, 20]" :page-size="queryInfo.count"
                layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>

        </el-card>



        <!-- 添加菜品信息 -->
        <el-dialog title="添加菜品信息" :visible.sync="visibleView.addInfo" @close="visibleView.addInfo = false">
            <el-form ref="foodInfoRef" :model="food">
                <el-form-item label="图片" :label-width="formLabelWidth">
                    <el-upload list-type="picture-card" :auto-upload="false" ref="pictureUpload"
                        :action="'../assets/images/' + food.foodName + '/' + timeid + '.jpg'">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                            <span class="el-upload-list__item-actions">
                                <span v-if="!disabled" class="el-upload-list__item-delete" @click="handleRemove(file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>

                </el-form-item>
                <el-form-item label="类型" :label-width="formLabelWidth">
                    <el-input v-model="food.foodTypeId"></el-input>
                </el-form-item>
                <el-form-item label="名称" :label-width="formLabelWidth">
                    <el-input v-model="food.foodName"></el-input>
                </el-form-item>
                <el-form-item label="价格" :label-width="formLabelWidth">
                    <el-input v-model="food.foodPrice"></el-input>
                </el-form-item>
                <el-form-item label="折扣" :label-width="formLabelWidth">
                    <el-input v-model="food.foodDiscount"></el-input>
                </el-form-item>
                <el-form-item label="销量" :label-width="formLabelWidth">
                    <el-input v-model="food.foodSales"></el-input>
                </el-form-item>
                <el-form-item label="重量" :label-width="formLabelWidth">
                    <el-input v-model="food.foodWeight"></el-input>
                </el-form-item>
                <el-form-item label="状态" :label-width="formLabelWidth">
                    <el-input v-model="food.foodState"></el-input>
                </el-form-item>



            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="visibleView.addInfo = false">取 消</el-button>
                <el-button type="primary" @click="addFoodInfo">确 定</el-button>
            </div>
        </el-dialog>

        <!-- 修改菜品信息 -->
        <el-dialog title="修改信息" :visible.sync="visibleView.editInfo" @close="visibleView.editInfo = false">
            <el-form ref="addFoodInfoRef" :model="foodInfo">
                <el-form-item label="菜品ID" :label-width="formLabelWidth">
                    <el-input v-model="foodInfo.foodId" disabled></el-input>
                </el-form-item>
                <el-form-item label="名称" :label-width="formLabelWidth">
                    <el-input v-model="foodInfo.foodName"></el-input>
                </el-form-item>
                <el-form-item label="价格" :label-width="formLabelWidth">
                    <el-input v-model="foodInfo.foodPrice"></el-input>
                </el-form-item>
                <el-form-item label="折扣" :label-width="formLabelWidth">
                    <el-input v-model="foodInfo.foodDiscount"></el-input>
                </el-form-item>
                <el-form-item label="销量" :label-width="formLabelWidth">
                    <el-input v-model="foodInfo.foodSales" disabled></el-input>
                </el-form-item>
                <el-form-item label="重量" :label-width="formLabelWidth">
                    <el-input v-model="foodInfo.foodWeight"></el-input>
                </el-form-item>
                <el-form-item label="状态" :label-width="formLabelWidth">
                    <el-input v-model="foodInfo.foodState"></el-input>
                </el-form-item>



            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="visibleView.editInfo = false">取 消</el-button>
                <el-button type="primary" @click="updataFoodInfo">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            total: 0,
            queryInfo: {
                query: '',
                page: 1,
                count: 5
            },
            visibleView: {
                editInfo: false,
                addInfo: false
            },
            foodInfo: {
                foodId: '',
                foodDiscount: '',
                foodImg: '',
                foodName: '',
                foodPrice: '',
                foodSales: '',
                foodWeight: '',
                foodState: ''
            },
            formLabelWidth: '120px',
            food: {
                foodId: '',
                foodDiscount: '',//
                foodImg: '',//
                foodName: '',//
                foodPrice: '',//
                foodSales: '',//
                foodWeight: '',//
                foodState: '',//
                foodTypeId: ''//
            },
            imgDialogImageUrl: '',
            disabled: false,
            timeid: 0
        }
    },
    created() {
        this.getAllfoods()
    },
    methods: {
        //获取菜品信息
        async getAllfoods() {
            const { data: res } = await this.$http.get('Food/getAllFood', { params: { page: this.queryInfo.page, count: this.queryInfo.count } })
            if (res.resultCode !== 200) return this.$message.error('获取列表失败')
            this.tableData = res.data.Foods
            this.total = res.data.AllFoodCount

        },
        addButton() {
            this.visibleView.addInfo = true
            this.putId()
        },
        //页数改变
        handleSizeChange(newPage) {
            this.queryInfo.count = newPage

            this.getAllfoods()
        },
        //页码值改变
        handleCurrentChange(newCount) {
            this.queryInfo.page = newCount
            this.getAllfoods()
        },
        async foodRemove(row) {
            const result = await this.$confirm('确认要删除菜品？', '提示', { confirmButtonText: '确认', cancelButtonText: '取消', type: 'warning' }).catch(err => err)

            if (result !== 'confirm') {
                return this.$message.info('取消删除')
            }
            const { data: res } = await this.$http.delete('Food/DeleteFoodInfo?foodId=' + row.foodId)
            if (res.resultCode !== 200) return this.$message.error('删除失败')
            this.getAllfoods()
        },
        //修改菜品信息（获取数据）
        foodEdit(row) {
            this.visibleView.editInfo = true
            this.foodInfo.foodId = row.foodId
            this.foodInfo.foodDiscount = row.foodDiscount
            this.foodInfo.foodImg = row.foodImg
            this.foodInfo.foodName = row.foodName
            this.foodInfo.foodPrice = row.foodPrice
            this.foodInfo.foodSales = row.foodSales
            this.foodInfo.foodWeight = row.foodWeight
            this.foodInfo.foodState = row.foodState
        },
        //修改菜品信息
        async updataFoodInfo() {
            const { data: res } = await this.$http.put('Food/UpdateFoodInfo', this.foodInfo, this.foodInfo.foodImg)
            if (res.resultCode !== 200) return this.$message.warning('修改失败')
            this.$message.success('修改成功')
            this.getAllfoods()
            this.visibleView.editInfo = false
        },
        //删除图片
        handleRemove(file) {
            let uploadFiles = this.$refs.pictureUpload.uploadFiles
            for (var i = 0; i < uploadFiles.length; i++) {
                if (uploadFiles[i]['url'] == file.url) {
                    uploadFiles.splice(i, 1)
                }
            }

        },
        //图片上传
        uploadSuccess() {
            // console.log(this.$refs.uploadMainImage)
            this.food.foodImg = this.$refs.uploadMainImage.uploadFiles[0].data
        },
        //添加菜品
        addFoodInfo() {
            this.$refs.foodInfoRef.validate(async (valid) => {
                this.food.foodImg = this.$refs.pictureUpload.uploadFiles[0].url
                if (!valid) return
                // axios.interceptors.request.use(config => {
                //     config.headers["content-type"] = 'multipart/form-data'
                //     return config;
                // })
                console.log(this.food)
                const { data: res } = await this.$http.post('Food/addFood', this.food, this.food.foodImg);
                // ,{
                //     headers: {
                //         "Content-type": "multipart/form-data"
                //     }
                console.log(res)

                if (res.resultCode !== 200) return this.$message.error('信息有误，注册失败！')
                this.$message.success('添加成功')
                this.visibleView.addInfo = false

            })
        },
        //生成一个日期时间随机数
        putId() {
            let date = Date.now();
            let rund = Math.ceil(Math.random() * 1000)
            this.timeid = date + '' + rund;
        },
        // onBeforeUpload(file){
        //     console.log(file)
        // }
    }
}
</script>

<style lang="scss" scoped>
// .container{
//     // overflow: scroll;
//     height: 1000%;
// }
</style>