<template>
    <div class="index-container">
        <!-- 中间 -->
        <div class="index-operation">
            <!-- 轮播图 -->
            <div class="index-carousel">
                <el-carousel :interval="3000" arrow="hover" class="carousel">
                    <el-carousel-item v-for="(item, index) in imageList" :key="index">
                        <img :src="item" alt="" class="carousel-img">
                    </el-carousel-item>
                </el-carousel>
            </div>

            <!-- 用户操作区域 -->
            <div class="index-user-operation">
                <el-row type="flex" justify="space-between">
                    <el-col :span="8">
                        <el-button @click="visibleView.anonymous = true">在线点餐</el-button>
                    </el-col>
                    <el-col :span="8">
                        <el-button @click="invest">充值</el-button>
                    </el-col>
                    <el-col :span="8">
                        <el-button @click="visibleView.loginDialog = true">会员登陆</el-button>
                    </el-col>
                </el-row>
            </div>
        </div>

        <!-- 登录Dialog -->
        <!--  -->
        <el-dialog title="会员登录" :visible.sync="visibleView.loginDialog" @close="loginCancelButton">
            <el-form ref="loginFormRef" :model="loginForm" :rules="loginRules">
                <el-form-item label="用户名" :label-width="formLabelWidth" prop="userName">
                    <el-input v-model="loginForm.userName"></el-input>
                </el-form-item>
                <el-form-item label="密码" :label-width="formLabelWidth" prop="userPassword">
                    <el-input v-model="loginForm.userPassword" show-password></el-input>
                </el-form-item>
                <el-row class="add-user">
                    <el-link type="info" @click="visibleView.register = true" :underline="false">新用户？加入会员</el-link>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="loginCancelButton">取 消</el-button>
                <el-button type="primary" @click="loginUser">确 定</el-button>
            </div>
        </el-dialog>


        <!-- 注册Dialog -->
        <el-dialog title="会员注册" :visible.sync="visibleView.register" @close="registerCancelButton">
            <el-form ref="registerFormRef" :model="registerForm" :rules="registerFormRules">
                <el-form-item label="用户名" :label-width="formLabelWidth" prop="userName">
                    <el-input v-model="registerForm.userName"></el-input>
                </el-form-item>
                <el-form-item label="密码" :label-width="formLabelWidth" prop="userPassword">
                    <el-input v-model="registerForm.userPassword" show-password></el-input>
                </el-form-item>
                <el-form-item label="手机号" :label-width="formLabelWidth" prop="userPhone">
                    <el-input v-model="registerForm.userPhone"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="registerCancelButton">取 消</el-button>
                <el-button type="primary" @click="registerUser">确 定</el-button>
            </div>
        </el-dialog>

        <!-- 直接点餐 -->
        <el-dialog title="在线点餐" :visible.sync="visibleView.anonymous">
            <div>先登录账号咩？</div>

            <div slot="footer" class="dialog-footer">
                <el-button @click="orderJump">直接点餐</el-button>
                <el-button type="primary" @click="loginJump">会员登陆</el-button>
            </div>

        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            //轮播图
            imageList: [
                require("../assets/images/img01.jpg"),
                require("../assets/images/img02.jpg"),
                require("../assets/images/img00.jpg"),
                require("../assets/images/img03.jpg"),
                require("../assets/images/img04.jpg"),
                require("../assets/images/img05.jpg")
            ],
            formLabelWidth: '120px',
            //视图
            visibleView: {
                loginDialog: false,
                register: false,
                anonymous: false
            },
            //用户登录信息
            loginForm: {
                userName: '',
                userPassword: ''
            },
            //用户注册信息
            registerForm: {
                userName: '',
                userPassword: '',
                userPhone: ''
            },
            //用户登录验证信息
            loginRules: {
                userName: [
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    // { min: 4, max: 18, message: '长度在 4 到 18 个字符', trigger: 'blur' }
                ],
                userPassword: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    // { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
                ],
            },
            //用户注册验证信息
            registerFormRules: {
                userName: [
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    // { min: 4, max: 18, message: '长度在 4 到 18 个字符', trigger: 'blur' }
                ],
                userPassword: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    // { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
                ],
                userPhone: [
                    { required: true, message: '请输入手机号', trigger: 'blur' },
                ]
            }
        }
    },
    methods: {
        //用户登录
        async loginUser() {
            this.$refs.loginFormRef.validate(async (valid) => {
                if (!valid) return;
                const { data: res } = await this.$http.post('User/Login', this.loginForm);
                if (res.resultCode !== 200) return this.$message.error('账户或密码有误！');
                this.$message.success('登录成功');
                //存用户json
                window.sessionStorage.setItem('UserInfo', JSON.stringify(res.data))
                //跳转
                if(res.data.userRole !== "1")
                    return this.$router.push("/admin")
                
                this.$router.push("/order")

            })
        },
        //关闭和取消登录窗体
        loginCancelButton() {
            //清除验证规则
            this.$refs['loginFormRef'].resetFields()
            //关闭Dialog
            this.visibleView.loginDialog = false
            //清空用户输入信息
            this.loginForm.userName = null
            this.loginForm.userPassword = null
        },
        //关闭和取消注册窗体
        registerCancelButton() {
            //清除验证规则
            this.$refs['registerFormRef'].resetFields()
            //关闭Dialog
            this.visibleView.register = false
            //清空注册信息
            this.registerForm.userName = null
            this.registerForm.userPassword = null
            this.registerForm.userPhone = null
        },
        //用户注册
        registerUser() {
            this.$refs.registerFormRef.validate(async (valid) => {
                if (!valid) return
                const { data: res } = await this.$http.post('User/Register', this.registerForm);
                // console.log(res)
                if (res.resultCode === 400) return this.$message.warning('用户名重复，请修改！')
                if (res.resultCode !== 200) return this.$message.error('信息有误，注册失败！')
                this.$message.success('注册成功')
                this.visibleView.register = false

            })
        },
        //充值事件
        invest() {
            this.$message.error('充值窗口暂时关闭')
        },
        //直接登录改会员登录
        loginJump() {
            this.visibleView.anonymous = false
            this.visibleView.loginDialog = true
        },
        //直接点餐
        orderJump() {
            //匿名账户
            const data = {
                userAvatar: 'defaultAvatar.jpg',
                userId: 0,
                userName: '匿名用户',
                userPhone: '',
                userSlogan: '',
                userToken: ''
            }
            //session
            window.sessionStorage.setItem('UserInfo', JSON.stringify(data))
            //跳转
            this.$router.push("/order")
        }
    }
}
</script>

<style lang="scss" scoped>
.index-container {
    background: url(../../src/assets/images/background2.svg);
    background-size: cover;
    height: 100%;
}

.index-operation {
    background-color: #FFF;
    box-shadow: 0 0 30px #ddd;
    width: 900px;
    height: 550px;
    border-radius: 10px;
    padding: 30px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.carousel-img {
    height: 100%;
    width: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.index-user-operation {
    padding-top: 30px;

    .el-row {
        height: 200px;
    }

    .el-button {
        width: 100%;
        height: 100%;
        border-radius: 0px;
        font-size: 30px;
    }
}

.add-user {
    text-align: right;
}
</style>