<template>
    <div class="container">
        <el-row type="flex" justify="space-around" :gutter="20">
            <el-col :span="2" v-for="(item, index) in menuList" :key="item.foodTypeId">
                <el-button @click="getFoodInfo(index + 1)">{{ item.foodTypeName }}</el-button>
            </el-col>
        </el-row>
        <el-empty description="没有食物捏" v-if="foodIsNull"></el-empty>
        <!-- 放卡片 -->
        <div class="cardInfo">
            <el-row type="flex" justify="start" :gutter="20">
                <el-col :span="6" v-for="(item) in foodList" :key="item.foodId">
                    <el-card :body-style="{ padding: '0px' }" shadow="always">
                        <img class="image" :src="require('../assets/images/' + item.foodImgSrc)">
                        <div class="menu-info">
                            <div class="menu-name">{{ item.foodName }}</div>
                            <div class="menu-sales">月销{{ item.foodSales }}</div>
                            <div class="menu-price">
                                <span>￥{{ item.foodPrice }}/份</span>
                                <!-- <span>{{item.foodNum}}</span> -->
                                <span style="float:right"><el-button type="danger" icon="el-icon-plus" circle
                                        @click="foodAddOrderList(item)"></el-button></span>
                            </div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>
        </div>

    </div>
</template>

<script>
export default {
    data() {
        return {
            menuList: [],
            foodList: [],
            orderList: [],//存放用户提交信息（订单）
            foodIsNull: false
        }
    },
    created() {
        this.getMenuList()
        this.getFoodInfo(1)
    },
    methods: {
        //获取所有菜单
        async getMenuList() {
            const { data: res } = await this.$http.get('Food/getAllFoodType')
            if (res.resultCode !== 200) return this.$message.error("菜单列表获取失败！")
            this.menuList = res.data


        },
        //获取菜品信息
        async getFoodInfo(index) {
            const { data: res } = await this.$http.get('Food/getFoodByType?type=' + index);
            if (res.resultCode !== 200) {
                this.foodList = null
                return this.foodIsNull = true;
            }
            this.foodIsNull = false
            this.foodList = res.data

            //添加用户选择食物数量
            this.foodList.forEach(element => {
                element.foodNum = 0
            });
            // console.log(this.foodList)
        },
        //添加到订单
        foodAddOrderList(item) {
            // console.log(item)
            var flag = 1
            item.foodNum++


            this.orderList.forEach(element => {
                if (element.foodId == item.foodId) {
                    element.foodNum = item.foodNum
                    element.foodAllPrice = item.foodNum * item.foodPrice
                    flag = 2
                }
            })



            if (flag === 1) {
                const list = {}
                list.foodId = item.foodId
                list.foodName = item.foodName
                list.foodNum = item.foodNum
                list.foodPrice = item.foodPrice
                list.foodAllPrice = item.foodNum * item.foodPrice
                this.orderList.push(list)
            }


            // console.log(this.orderList)
            // this.$message.success('已添加')

            window.sessionStorage.setItem("OrderList", JSON.stringify(this.orderList))
        }
    }
}
</script>

<style lang="scss" scoped>
.container {
    // padding: 20px;
    // background-color: red;
    overflow: hidden;

    >.el-row {

        flex-wrap: wrap;
        flex-direction: row;

        .el-button {
            border: 0px;
            border-radius: 0px;
            text-align: left;
            padding: 0px;
        }

    }

    .cardInfo {

        // margin-left: 80px;
        >.el-row {
            flex-wrap: wrap;
            flex-direction: row;
        }
    }



}

.image {
    display: block;
    width: 100%;
    height: 129.97px;
    object-fit: cover;
}

.menu-info {
    padding: 14px;

    >div {
        margin: 10px auto;
    }

}


.menu-name {
    font-size: 20px;
}

.menu-sales {
    font-size: 15px;
    color: #aaa;
}

.menu-price {
    font-size: 20px;
}

.el-card {
    margin-top: 30px;
    // margin-left: 80px;
}
</style>