import Vue from 'vue'
import VueRouter from 'vue-router'

import indexView from '../components/indexView.vue'
import orderView from '../components/orderView.vue'
import foodView from '../components/foodView.vue'
import overView from '../components/overView.vue'


//管理员
import adminView from '../components/adminView.vue'
import userManage from '../components/userManageView.vue'
import foodManage from '../components/foodManageView.vue'
import orderManage from '../components/orderManageView.vue'
import welcomeManage from '../components/adminWelcome.vue'

Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    component:indexView
  },
  {
    path:'/order',
    component:orderView,
    redirect: '/food',
    children:[
      {
        path:'/food',
        component:foodView,
      }
    ]
  },
  {
    path:'/admin',
    component:adminView,
    redirect: '/usermanage',
    children:[
      {
        path:'/usermanage',
        component:userManage
      },
      {
        path:'/foodmanage',
        component:foodManage
      },
      {
        path:'/ordermanage',
        component:orderManage
      },
      {
        path:'/welcomemanage',
        component:welcomeManage
      }
    ]
  },
  {
    path:'/over',
    component:overView
  }

]

const router = new VueRouter({
  routes
})


router.beforeEach((to, from, next) => {
  if (to.path === '/') return next()
  // 获取userInfo
  const userInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))
  if (!userInfo) return next('/')
  next()
})

export default router
