import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './plugins/element.js'

import './assets/css/global.css'

import axios from 'axios'

axios.defaults.baseURL = 'api'
axios.interceptors.request.use(config=>{
  if(JSON.parse(window.sessionStorage.getItem('UserInfo')) != null)
    config.headers.Authorization = JSON.parse(window.sessionStorage.getItem('UserInfo')).userToken
    // config.headers["content-type"] = 'multipart/form-data;boundary=----WebKitFormBoundaryvYBr7sgeBp3ta0y5'
  return config;
})
Vue.prototype.$http = axios


Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
