<template>
    <div class="container">
        <el-container>
            <el-header>
                <div>在线点餐系统</div>
                <div>
                    欢迎你，{{ UserInfo.userName }}
                    <el-dropdown trigger="hover" @command="handleCommand">
                        <el-button>
                            <i class="el-icon-more"></i>
                        </el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="1">修改信息</el-dropdown-item>
                            <el-dropdown-item command="2">退出</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-header>
            <el-main>
                <!-- 放路由 -->
                <router-view></router-view>
            </el-main>
            <el-footer>
                <!-- 订单列表 -->
                <el-button @click="orderAccount" icon="el-icon-food"></el-button>
            </el-footer>

            <!-- 修改信息Dialog -->
            <!-- 关闭窗体 -->
            <el-dialog title="修改信息" :visible.sync="editUserInfoVisible">
                <el-form ref="editFormRef" :model="editForm">
                    <el-form-item label="用户名" :label-width="formLabelWidth">
                        <el-input v-model="editForm.userName" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="个性签名" :label-width="formLabelWidth">
                        <el-input v-model="editForm.userSlogan"></el-input>
                    </el-form-item>
                    <el-form-item label="电话" :label-width="formLabelWidth">
                        <el-input v-model="editForm.userPhone"></el-input>
                    </el-form-item>
                    <el-form-item label="邮箱" :label-width="formLabelWidth">
                        <el-input v-model="editForm.userEmail"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="editUserInfoVisible = false">取 消</el-button>
                    <el-button type="primary" @click="editUser">确 定</el-button>
                </div>
            </el-dialog>


            <!-- 订单 -->
            <el-drawer title="我是标题" :visible.sync="drawer" :with-header="false" direction="btt">
                <el-table :data="OrderList" style="width: 100%" :show-header="false">
                    <el-table-column prop="foodName" label="日期" width="180">
                    </el-table-column>
                    <el-table-column prop="foodPrice" label="姓名" width="180">
                    </el-table-column>
                    <el-table-column prop="foodNum" label="数量">
                    </el-table-column>
                    <el-table-column prop="foodAllPrice" label="总价">
                    </el-table-column>
                </el-table>

                <el-row>
                    <el-col :span="12">总价：{{ totalPrice }}</el-col>
                    <el-col :span="12"><el-button @click="submitOrder">结算</el-button></el-col>
                </el-row>




            </el-drawer>
        </el-container>
    </div>
</template>

<script>
export default {
    data() {
        return {
            UserInfo: [],
            editUserInfoVisible: false,
            formLabelWidth: '120px',
            editForm: {
                userId:'',
                userName: '',
                userPhone: '',
                userEmail: '',
                userSlogan: '',
                userAvatar: ''
            },
            drawer: false,
            OrderList: [],
            totalPrice: 0
        }

    },
    created() {
        this.getUserInfo()
    },
    methods: {
        handleCommand(command) {
            if (command == 1) {
                this.editUserInfoVisible = true
                //修改信息
                if (JSON.parse(window.sessionStorage.getItem('UserInfo')).userId === 0) return this.$message.error("未登录会员！不可修改")//未登录不可修改账户信息
                this.editForm.userId = this.UserInfo.userId
                this.editForm.userName = this.UserInfo.userName
                this.editForm.userPhone = this.UserInfo.userPhone
                this.editForm.userEmail = this.UserInfo.userEmail
                // this.editForm.userSlogan = this.UserInfo.userSlogan

            } else {
                //去除存储信息
                window.sessionStorage.removeItem('UserInfo')
                this.$router.go(-1)
            }

        },
        //获取session中用户信息
        getUserInfo() {
            //转为json接收
            this.UserInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))
            // console.log(JSON.parse(window.sessionStorage.getItem('UserInfo')))
        },
        //修改用户信息
        async editUser() {
            //提交修改信息
            const {data:res} = await this.$http.put('User/UpdateUserInfo' ,this.editForm)
            if (res.resultCode !== 200) return this.$message.warning('修改失败')
            console.log(res)
            this.$message.success('修改成功')
            this.editUserInfoVisible = false
            //session重新获取新数据
            const { data: resnew } = await this.$http.post('User/Login', this.editForm);
            window.sessionStorage.setItem('UserInfo', JSON.stringify(resnew.data))
        },
        //订单详情按钮
        orderAccount() {
            //把订单信息put
            this.drawer = true
            this.OrderList = JSON.parse(window.sessionStorage.getItem('OrderList'))
            this.totalPrice = 0
            this.OrderList.forEach(element => {

                this.totalPrice += element.foodAllPrice
            })
            // console.log(this.OrderList)
            //提示完成提交
        },
        //结算
        async submitOrder() {
            var data = ''
            this.OrderList.forEach(element => {
                data += element.foodId + '*' + element.foodNum + ','
            })
            data = data.substr(0, data.length - 1)
            const order = {
                orderFoodId: data,
                orderTotalPrice: this.totalPrice,
                orderUserId: this.UserInfo.userId
            }
            // console.log(order)
            const { data: res } = await this.$http.post('Order/addOrder', order);
            // console.log(res)
            if (res.resultCode === 400) return this.$message.warning('有订单进行中!');
            if (res.resultCode !== 200) return this.$message.error('下单失败!');
            // console.log(res)


            window.sessionStorage.removeItem('OrderList')

            this.$router.push("/over")




        }
    }
}
</script>

<style lang="scss" scoped>
.container {
    height: 100%;
}

.el-container {
    height: 100%;
}

.el-drawer {

    .el-row {
        font-size: 30px;
        margin-top: 50px;

        // float: right;
        .el-col:nth-child(2) {
            text-align: right;

            .el-button {
                width: 200px;
                height: 50px;
                font-size: 20px;
            }
        }
    }

    .el-row,
    .el-table {
        margin: 30px;
    }


}

.el-header {
    // background-color: aqua;
    line-height: 60px;
    display: flex;
    justify-content: space-between;
}


.el-footer {
    // background-color: red;
    line-height: 60px;

    .el-button {
        // width: 100%;
        float: right;
        border-radius: 0px;

    }
}
</style>