<template>
    <div>
        <el-row>下单成功</el-row>
        <el-row>请评价</el-row>
        <el-row>
            <el-rate v-model="starValue" allow-half @change="changeStar"></el-rate>
        </el-row>
        <el-row>
            <el-input type="textarea" :rows="2" placeholder="请输入内容" v-model="textarea" maxlength="30" clearable>
            </el-input>
        </el-row>
        <el-row>
            <el-button @click="submit">提交</el-button>
            <el-button @click="goHome">返回</el-button>
        </el-row>
    </div>
</template>

<script>
export default {
    data() {
        return {
            textarea: '',
            starValue: 0
        }
    },
    methods: {
        changeStar() {
            console.log(this.starValue)
        },
        goHome(){
            window.sessionStorage.removeItem('UserInfo')
            this.$router.push("/")
        },
        //提交评价
        async submit(){
            const order = {
                orderUserId:JSON.parse(window.sessionStorage.getItem('UserInfo')).userId,
                orderEvaluation:this.textarea,
                orderStar:this.starValue
            }
            console.log(order)
            const { data: res } = await this.$http.post('Order/EvaluateOrder', order);

            // console.log(res)
            if (res.resultCode !== 200) return this.$message.error("评价失败！")
            this.$message.success('成功提交评价!谢谢')
            this.$router.push("/")
        }
    }
}
</script>

<style lang="scss" scoped>
div {
    >.el-row {

        text-align: center;
    }

    >.el-row:nth-child(1) {
        font-size: 40px;
    }

    >.el-row:nth-child(2) {
        font-size: 30px;
    }

    >.el-row:nth-child(4) {
        width: 30%;
        margin: 80px auto;
    }

    >.el-row:nth-child(3) {
        /deep/ .el-rate__icon {
            font-size: 60px;
        }
    }

    >.el-row {
        margin-top: 30px;
    }
}

.el-button{
    width: 15%;
}
</style>