<template>
    <div>
        <!-- 面包屑 -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/admin' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>订单管理</el-breadcrumb-item>
        </el-breadcrumb>


        <!-- 订单列表 -->

        <el-card class="box-card">

            <!-- <el-row>
                    <el-col :span="24">
                        <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable>
                            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                        </el-input>
                    </el-col>
                    <el-col :span="4">
                        <el-button type="primary">添加菜单</el-button>
                    </el-col>
                </el-row> -->

            <el-table border stripe :data="tableData">
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="orderId" label="订单编号"></el-table-column>
                <el-table-column prop="orderUserId" label="下单用户ID"></el-table-column>
                <el-table-column prop="orderTotalPrice" label="总价"></el-table-column>
                <el-table-column prop="orderTime" label="时间"></el-table-column>
                <el-table-column label="评分">
                    <template slot-scope="scope">
                        <el-rate v-model="scope.row.orderStar" disabled show-score text-color="#ff9900" :score-template="scope.row.orderStar +''">
                    </el-rate>
                    </template>    
                </el-table-column>
                <el-table-column prop="orderEvaluation" label="评价内容"></el-table-column>
                <el-table-column prop="" label="操作">
                    <!--  -->
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-info" size="small"
                            @click="orderInfo(scope.row)"></el-button>
                        <el-button type="danger" icon="el-icon-delete-solid" size="small"
                            @click="removeOrder(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>


            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page"
                :page-sizes="[5, 10, 20]" :page-size="count" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>

        </el-card>


        <!-- 订单详细信息 -->
        <el-dialog title="订单详细信息" :visible.sync="visibleView.OrderInfo" @close="visibleView.OrderInfo = false">

            <el-table border stripe :data="tableInfo">
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="foodName" label="商品名称"></el-table-column>
                <el-table-column prop="foodPrice" label="价格"></el-table-column>
                <el-table-column prop="foodCount" label="数量"></el-table-column>
            </el-table>
            <div slot="footer" class="dialog-footer">
                <el-button @click="visibleView.OrderInfo = false">关 闭</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            count: 0,
            page: 0,
            total: 0,
            visibleView: {
                OrderInfo: false
            },
            tableInfo: []
        }
    },
    created() {
        this.getAllOrders()
    },
    methods: {
        async getAllOrders() {
            const { data: res } = await this.$http.post('Order/getAllOrder')
            if (res.resultCode !== 200) return this.$message.error('获取列表失败')
            console.log(res)
            this.tableData = res.data
            this.total = res.data.length

            console.log(this.tableData)

        },//页数改变
        handleSizeChange(newPage) {
            this.count = newPage

            this.getAllOrders()
        },
        //页码值改变
        handleCurrentChange(newCount) {
            this.page = newCount
            // console.log(newCount)
            this.getAllOrders()
        },
        orderInfo(row) {
            //显示窗体
            this.tableInfo.length = 0
            this.visibleView.OrderInfo = true
            console.log(row.foodInfos)
            row.foodInfos.forEach(element => {
                this.tableInfo.push({ "foodName": element.foodName, "foodCount": element.foodCount, "foodPrice": element.foodPrice })
            });
            // this.tableInfo.push({foodName:row.foodInfos.foodName,foodCount:row.foodInfos.foodCount,foodPrice:row.foodInfos.foodPrice})
            // console.log(this.tableInfo)

        },
        async removeOrder(row) {
            const result = await this.$confirm('确认要删除订单？', '提示', { confirmButtonText: '确认', cancelButtonText: '取消', type: 'warning' }).catch(err => err)
            console.log(row)

            if (result !== 'confirm') {
                return this.$message.info('取消删除')
            }
            const { data: res } = await this.$http.delete('Order/DeleteOrderInfo?orderId=' + row.orderId)
            if (res.resultCode !== 200) return this.$message.error('删除失败')
            this.getAllOrders()
        }
    }
}
</script>

<style lang="scss" scoped>

</style>