<template>
    <div>
        <!-- 面包屑 -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/admin' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        </el-breadcrumb>



        <!-- 视图 -->
   
        <el-card class="box-card">
        
 <!-- search -->

            <!-- <el-row>
                <el-col :span="24">
                    <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable>
                        <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary">添加用户</el-button>
                </el-col>
            </el-row> -->

            <el-table border stripe :data="tableData">
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="userName" label="用户名"></el-table-column>
                <el-table-column prop="userPhone" label="手机号"></el-table-column>
                <el-table-column prop="userRole" label="角色"></el-table-column>
                <el-table-column prop="" label="操作">
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-info" size="small"
                            @click="UserInfoShow(scope.row)"></el-button>
                        <el-button type="warning" icon="el-icon-edit" size="small"
                            @click="UserInfoEdit(scope.row)"></el-button>
                        <el-button type="danger" icon="el-icon-delete-solid" size="small"
                            @click="removeUser(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!-- 分页 -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="queryInfo.page" :page-sizes="[5, 10, 20]" :page-size="queryInfo.count"
                layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>

        </el-card>

        <!-- 用户详细信息 -->

        <el-dialog title="用户信息" :visible.sync="visibleView.UserInfo" @close="visibleView.UserInfo = false">
            <el-row>头像</el-row>

            <el-row>用户ID：{{ userInfo.userId }}</el-row>
            <el-row>用户名：{{ userInfo.userName }}</el-row>
            <el-row>邮箱:{{ userInfo.userEmail }}</el-row>
            <el-row>电话:{{ userInfo.userPhone }}</el-row>
            <el-row>角色:{{ userInfo.userRole }}</el-row>
            <el-row>创建时间:{{ userInfo.userCreateTime }}</el-row>
            <el-row>个性签名:{{ userInfo.userSlogan }}</el-row>
            <div slot="footer" class="dialog-footer">
                <el-button @click="visibleView.UserInfo = false">关 闭</el-button>
            </div>
        </el-dialog>

        <!-- 修改用户信息 -->
        <el-dialog title="修改信息" :visible.sync="visibleView.editInfo" @close="visibleView.editInfo = false">
            <el-form ref="userInfoRef" :model="userInfo">
                <el-form-item label="用户ID" :label-width="formLabelWidth">
                    <el-input v-model="userInfo.userId" disabled></el-input>
                </el-form-item>
                <el-form-item label="用户名" :label-width="formLabelWidth">
                    <el-input v-model="userInfo.userName" disabled></el-input>
                </el-form-item>
                <el-form-item label="邮箱" :label-width="formLabelWidth">
                    <el-input v-model="userInfo.userEmail"></el-input>
                </el-form-item>
                <el-form-item label="电话" :label-width="formLabelWidth">
                    <el-input v-model="userInfo.userPhone"></el-input>
                </el-form-item>
                <el-form-item label="角色" :label-width="formLabelWidth">
                    <el-input v-model="userInfo.userRole" disabled></el-input>
                </el-form-item>
                <el-form-item label="创建时间" :label-width="formLabelWidth">
                    <el-input v-model="userInfo.userCreateTime" disabled></el-input>
                </el-form-item>
                <el-form-item label="个性签名" :label-width="formLabelWidth">
                    <el-input v-model="userInfo.userSlogan"></el-input>
                </el-form-item>


            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="visibleView.editInfo = false">取 消</el-button>
                <el-button type="primary" @click="updataUserInfo">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

export default {
    data() {
        return {
            queryInfo: {
                query: '',
                page: 1,
                count: 5
            },
            total: 10,
            tableData: [],
            visibleView: {
                UserInfo: false,
                editInfo: false
            },
            userInfo: {
                userAvatar: '',
                userCreateTime: '',
                userEmail: '',
                userId: '',
                userName: '',
                userPhone: '',
                userRole: '',
                userSlogan: '',
            },
            formLabelWidth: '120px'
        }
    },
    created() {
        this.getUserList()
    },
    methods: {
        //async
        async getUserList() {
            const { data: res } = await this.$http.get('User/AllUserByPage', { params: { page: this.queryInfo.page, count: this.queryInfo.count } })
            if (res.resultCode !== 200) return this.$message.warning('!')
            this.total = res.data.AllUserCount

            this.tableData = res.data.Users
            this.tableData.forEach(element => {
                element.userRole = element.userRole === "1" ? '会员' : '管理员'
            });
            console.log(this.tableData)
        },
        //页数改变
        handleSizeChange(newPage) {
            this.queryInfo.count = newPage

            this.getUserList()
        },
        //页码值改变
        handleCurrentChange(newCount) {
            this.queryInfo.page = newCount
            // console.log(newCount)
            this.getUserList()
        },
        //获取用户信息
        getUserInfo(row) {
            this.userInfo.userAvatar = row.userAvatar
            this.userInfo.userCreateTime = row.userCreateTime
            this.userInfo.userEmail = row.userEmail
            this.userInfo.userId = row.userId
            this.userInfo.userName = row.userName
            this.userInfo.userPhone = row.userPhone
            this.userInfo.userRole = row.userRole
            this.userInfo.userSlogan = row.userSlogan
        },
        //查看用户信息
        UserInfoShow(row) {
            // console.log(id)
            //通过id获取信息
            this.visibleView.UserInfo = true
            this.getUserInfo(row)
        },
        UserInfoEdit(row) {
            this.visibleView.editInfo = true
            this.getUserInfo(row)
        },
        //修改用户信息
        async updataUserInfo() {

            console.log(this.userInfo)
            const {data:res} = await this.$http.put('User/UpdateUserInfo' ,this.userInfo)
            if (res.resultCode !== 200) return this.$message.warning('修改失败')
            console.log(res)
            this.$message.success('修改成功')
            this.getUserList()
            this.visibleView.editInfo = false
        },
        //删除用户
        async removeUser(row) {
            const result = await this.$confirm('确认要删除用户？', '提示', { confirmButtonText: '确认', cancelButtonText: '取消', type: 'warning' }).catch(err => err)
            // console.log(result)

            if(result !== 'confirm'){
                return this.$message.info('取消删除')
            }
            const { data: res } = await this.$http.delete('User/DeleteUserInfo?userId=' + row.userId)
            if (res.resultCode !== 200) return this.$message.error('删除失败')
            this.getUserList()


        }
    }
}
</script>

<style lang="scss" scoped>

</style>