<template>
    <el-container class="container-home">
        <!-- 头部 -->
        <el-header>
            <div style="color:#eee">管理员界面</div>
            <div style="color:#eee">
                欢迎你，{{ UserInfo.userName }}
                <el-dropdown trigger="hover" @command="handleCommand">
                    <el-button type="info">
                        <i class="el-icon-more"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item >退出</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </el-header>
        <el-container>
            <el-aside width="200px">
                <el-menu active-text-color="#0099CC" unique-opened router background-color="#545c64" text-color="#eee"
                    :default-active="$route.path">
                    <el-menu-item :index="'/usermanage'">
                        <span slot="title">用户管理</span>
                    </el-menu-item>
                    <el-menu-item :index="'/ordermanage'">
                        <span slot="title">订单管理</span>
                    </el-menu-item>
                    <el-menu-item :index="'/foodmanage'">
                        <span slot="title">菜单管理</span>
                    </el-menu-item>
                    
                </el-menu>
            </el-aside>
            <!-- 主体 -->
            <el-main>
                <!-- 路由 -->
                <router-view></router-view>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
export default {
    data() {
        return {
            menuList: [],
            UserInfo: [],
        }
    },
    created() {
        this.getUserInfo()
    },
    methods: {
        //获取用户信息
        getUserInfo() {
            this.UserInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))
        },
        handleCommand() {
            window.sessionStorage.removeItem('UserInfo')
            this.$router.push('/')
        }
    }
}
</script>

<style lang="scss" scoped>
.el-header {
    background-color: #545c64;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.el-aside {
    background-color: #545c64;
    height: 100%;

    .el-menu {
        border-right: none
    }
}

.el-main {
    // background-color:#eee;
    height: 200%;
    
    // .router-view{
    //     height: 100%;
    // }
}

.container-home {
    height: 100%;
    overflow: hidden
}
</style>